@use "../abstracts/breakpoints";
@use "../abstracts/variables";
.woocommerce-tabs {
    position: relative;
    padding: 0 0 2.5rem 0;
    &::before {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        background: #f1f0f0;
        width: 100vw;
        z-index: -1;
        height: 100%;
        left: 50%;
        overflow: hidden;
        display: block;
        transform: translateX(-50%);
    }
}

.woocommerce div.product .woocommerce-tabs ul.tabs li a {
    display: inline-block;
    padding: 1.1em 0rem 1.1em 0rem;
    font-size: 0.7rem;
    @include breakpoints.iphone {
        padding: 4.2em 0.4rem .5em 0.4rem;
        font-size: 0.7rem;
    }
    @include breakpoints.tablet {
        padding: 4.2em 1.2rem .5em 1.2rem;
        font-size: 0.77rem;
    }
    font-weight: 600;
    color: #515151;
    text-decoration: none;
}

.woocommerce div.product .woocommerce-tabs ul.tabs li {
    border: 0px solid #cfc8d8;
    background-color: transparent;
    color: #515151;
    display: block;
    @include breakpoints.iphone {
        display: inline-block;
    }
    position: relative;
    z-index: 0;
    border-radius: 0;
    margin: 0 -5px;
    padding: 0 1em;
}

.woocommerce div.product .woocommerce-tabs ul.tabs li {
    &::before {
        box-shadow: none;
        border-width: 0;
    }
    &::after {
        box-shadow: none;
        border-width: 0;
    }
    &.active::before {
        box-shadow: none;
    }
    &.active::after {
        box-shadow: none;
    }
}

.woocommerce div.product .woocommerce-tabs ul.tabs li.active {
    background: #fff;
    color: #515151;
    z-index: 2;
    border-bottom-color: variables.$secondary-color;
    border-bottom-width: 0px;
    @include breakpoints.iphone {
        border-bottom-width: 3px;
    }
}

.woocommerce div.product .woocommerce-tabs .panel {
    margin: 0 0 2em;
    padding: 0;
    @include breakpoints.tablet {
        padding: 1.45rem 1.35rem;
    }
    
}

.woocommerce div.product .woocommerce-tabs ul.tabs {
    padding: 0;
}
.woocommerce-Tabs-panel h2 {
    font-size: 1.45rem;
}

.woocommerce-tabs {
    &__mkd-grid-row {
        display: flex;
        flex-wrap: wrap;
    }
    &__mkd-grid-col-6 {
        width: 100%;
        @include breakpoints.tablet {
            width: 50%;
        }
        
    }
    &__mkd-grid-col-6.enq.left {
        width: 100%;
         @include breakpoints.tablet {
            width: 70%;
         }
        
    }
    &__mkd-grid-col-6.enq.right {
        width: 100%;
        @include breakpoints.tablet {
            width: 30%;
        }
    }
    &__mkd-grid-heading {
        font-size: 1.7rem;
        color: variables.$secondary-color;
    }
    &__mkd-grid-subheading {
        font-size: 1.4rem;
        padding: 0 0 0.5rem 0;
        color: variables.$secondary-color;
    }
    &__feat-blck {
        background: #fdfdfd;
        border: 1px solid #bfbfbf;
        padding: 1.0rem 1.5rem 1.0rem 1.5rem;
        margin-bottom: 10px;
    }
    // &__feat-blck ul.features li:nth-child(odd) {
    //     background: #f5f5f4;
    // }
    &__feat-blck  ul.features li {
        color: variables.$secondary-color;
        font-size: .88rem;
        line-height: 1.5rem;
        border-bottom: 1px solid #bfbfbf;
        padding: 6px 0;
        margin: 0;
    }
    &__feat-blck  ul.features li:last-child {
        border-bottom: 0px solid #bfbfbf;
    }
    &__specs {
        padding: 0;
        @include breakpoints.tablet {
            padding: 0 1.5rem 0 1.5rem;
        }
        color: variables.$secondary-color;
    }
    &__fullFeats {
        background: #fdfdfd;
        border: 1px solid #bfbfbf;
        padding: 1.0rem 1.5rem 1.0rem 1.5rem;
    }
    &__fullFeats li {
        font-size: .88rem;
        line-height: 1.5rem;
        color: variables.$secondary-color;
        border-bottom: 1px solid #bfbfbf;
        padding: 6px 0;
        margin: 0;
    }
    &__fullFeats li:last-child {
        border-bottom: 0px solid #bfbfbf;
    }
    &__form {
        margin:0;
    }
    &__contact-address {
        margin: 0;
        @include breakpoints.tablet {
            margin: 0 0 0 4rem;
        }
    }
    &__form-input {
        padding: 0.5rem;
        width: 100%;
        margin-bottom: 0.5rem;
    }
    &__form-textarea {
        padding: 0.5rem;
        width: 100%;
        margin-bottom: 0;
        height: 100px;
    }
    &__form-label {
        color: variables.$secondary-color;
        font-weight: 600;
    }
    &__contact-location {
        padding: 1rem 0 0 0;
    }
    &__submit {
        background: variables.$secondary-color;
        color: white;
        padding: 0.5rem 1.7rem;
        margin-top: 0.9rem;
        letter-spacing: 0.02rem;
        font-weight: 400;
    }
    &__description-wrap {
        display: flex;
        flex-wrap: wrap;
    }
    &__description {
        width: 100%;
        margin-right: 0%;
        @include breakpoints.tablet {
            width: 70%;
            margin-right: 5%;
        }
    }
    &__description p {
        font-size: 0.85rem;
    }
    &__benefits {
        width: 100%;
        @include breakpoints.tablet {
            width: 25%;
        }
    }
    &__benefits-text {
        margin-top: -0.1rem;
        font-size: 0.85rem;
    }
    &__benefits-text-list {
        border-bottom: 1px solid #666;
        padding: 0.3rem 0 0.5rem 0;
        line-height: 1.6rem;
    }
    &__benefits-text-list:nth-child(1){
        padding: 0 0 0.5rem 0;
    }
    &__downloads-heading {
        font-size: 1.05rem;
        font-weight: 700;
        color: variables.$secondary-color;
        text-decoration: none;
    }
    &__downloads-heading:hover {
        text-decoration: underline;
    }
    & .wpcf7-not-valid-tip {
        color: variables.$primary-color;
        font-style: italic;
        margin-bottom: -1.7rem;
        line-height: 1.2rem;
    }
    & .wpcf7 form .wpcf7-response-output {
        margin: 0.5em 0em 0 0;
        padding: 0.5em 1em;
        background: variables.$primary-color;
        color: white;
    }
    & .wpcf7 form.invalid .wpcf7-response-output {
        border-color: variables.$primary-color;
    }

}

.woocommerce-Tabs-panel {
    & table {
        width: 100%;
        border: 1px solid #e2e2e1;
        margin-bottom: 0;
        display: table;
        visibility: visible;
    }
    & table tr th {
        background: #fff;
        color: #ef9003;
        font-weight: 600;
        font-size: .7rem;
        padding: 0.8rem 0.6rem;
        @include breakpoints.tablet {
            font-size: .87rem;
            padding: 1.1rem 1rem;
        }
        text-align: left;
        border-right: 1px solid #ddd;
    }
    & table tr td {
        background: 0 0;
        font-size: .7rem;
        padding: 0.8rem 0.6rem;
        @include breakpoints.tablet {
            font-size: .87rem;
            padding: 1.1rem 1rem;
        }
        text-align: left;
        border-bottom: 1px solid #ddd;
        border-right: 1px solid #ddd;
    }
}
